import defaults from 'lodash/defaults';

import React, { ChangeEvent, PureComponent, SyntheticEvent } from 'react';
import { InlineFormLabel, LegacyForms, Select, Spinner, stylesFactory, Switch } from '@grafana/ui';
import { QueryEditorProps, SelectableValue } from '@grafana/data';
import { DataSource } from './DataSource';
import { defaultQuery, MyDataSourceOptions, MyQuery, Messaging } from './types';
import { Debounce } from 'lodash-decorators';
import { MqttClient } from 'mqtt';
import { css, cx } from 'emotion';

const { FormField } = LegacyForms;
const messagingOptions: Array<SelectableValue<string>> = [
  { label: Messaging.DEFAULT, value: Messaging.DEFAULT },
  { label: Messaging.REQ_RES, value: Messaging.REQ_RES },
];

type Props = QueryEditorProps<DataSource, MyQuery, MyDataSourceOptions>;
type States = {
  deviceId: string;
  pointOptions: Array<SelectableValue<string>>;
  loading: boolean;
};

export class QueryEditor extends PureComponent<Props, States> {
  client: MqttClient;

  constructor(props: Props) {
    super(props);
    const {
      datasource: { client, deviceId },
      query: { hasManualTopic },
    } = this.props;

    this.state = {
      deviceId,
      pointOptions: [],
      loading: !hasManualTopic,
    };

    this.client = client;
    this.subscribeOnTopic(hasManualTopic);
    this.subscribeMessage();
  }

  private subscribeOnTopic(hasManualTopic: boolean) {
    const { deviceId } = this.state;
    if (hasManualTopic) {
      this.setState({ loading: false });
    } else {
      this.setState({ loading: true });
    }
    if (!hasManualTopic && deviceId) {
      this.setState({ loading: true });
      if (this.client.disconnected) {
        this.client.reconnect();
      }
      this.client.unsubscribe(deviceId);
      this.client.subscribe(deviceId); // unsubscribe and subscribe: it gets `message` when we open editor
    }
  }

  componentDidMount(): void {
    // Execute query when we open editor, so it will query data for that editor and editor gets value
    this.handleMqttRunQuery();
  }

  private subscribeMessage() {
    const that = this;
    this.client.on('message', function(replyTopic: string, message: any) {
      const { deviceId } = that.state;
      if (deviceId === replyTopic) {
        try {
          const parsedMessage = JSON.parse(message.toString());
          const pointOptions: Array<SelectableValue<string>> = [];
          if (Array.isArray(parsedMessage)) {
            parsedMessage.forEach(item => {
              pointOptions.push({ label: item, value: item });
            });
          }
          that.setState({ pointOptions, loading: false });
        } catch (e) {}
      }
    });
  }

  @Debounce(1000)
  handleMqttRunQuery() {
    const { onRunQuery, onChange, query } = this.props;
    const { deviceId } = this.state;
    const { manualTopic, hasManualTopic, pointId } = query;
    let topic = '<<reserved>>'; // by default topic
    if (!hasManualTopic && pointId) {
      topic = `${deviceId}/${pointId}`;
    } else if (hasManualTopic && manualTopic) {
      topic = manualTopic;
    }
    onChange({ ...query, topic });
    onRunQuery();
  }

  onPointSelectionChange = (event: SelectableValue<string>) => {
    const { onChange, query } = this.props;
    onChange({ ...query, pointId: event.value });
    this.handleMqttRunQuery();
  };

  onManualTopicChange = (event: ChangeEvent<HTMLInputElement>) => {
    const { onChange, query } = this.props;
    onChange({ ...query, manualTopic: event.target.value });
    this.handleMqttRunQuery();
  };

  onChangeHasManualTopic = (event: SyntheticEvent<HTMLInputElement>) => {
    const { onChange, query } = this.props;
    const hasManualTopic = (event.target as HTMLInputElement).checked;
    onChange({ ...query, hasManualTopic });
    this.handleMqttRunQuery();
    this.subscribeOnTopic(hasManualTopic);
  };

  onChangeMessaging = (event: SelectableValue<string>) => {
    const { onChange, query, onRunQuery } = this.props;
    onChange({ ...query, messaging: event.value as Messaging });
    onRunQuery();
  };

  onChangeHasResponseSelector = (event: SyntheticEvent<HTMLInputElement>) => {
    const { onChange, query, onRunQuery } = this.props;
    onChange({ ...query, hasResponseSelector: (event.target as HTMLInputElement).checked });
    onRunQuery();
  };

  onMemberChange = (event: ChangeEvent<HTMLInputElement>) => {
    const { onChange, query, onRunQuery } = this.props;
    onChange({ ...query, member: event.target.value });
    onRunQuery();
  };

  onLegendChange = (event: ChangeEvent<HTMLInputElement>) => {
    const { onChange, query, onRunQuery } = this.props;
    onChange({ ...query, legend: event.target.value });
    onRunQuery();
  };

  render() {
    const query = defaults(this.props.query, defaultQuery);
    const { pointOptions, loading } = this.state;
    const { pointId, manualTopic, hasManualTopic, member, legend, messaging, hasResponseSelector } = query;
    const styles = this.getStyles();
    return (
      <div className="gf-form-group">
        <div className="gf-form">
          {!hasManualTopic ? (
            <>
              <InlineFormLabel width={12}>Select Point ID</InlineFormLabel>
              <Select
                width={24}
                options={pointOptions}
                onChange={this.onPointSelectionChange}
                value={pointId}
                disabled={loading}
                menuPlacement="bottom"
              />
            </>
          ) : (
            <FormField
              inputWidth={12}
              labelWidth={12}
              value={manualTopic}
              onChange={this.onManualTopicChange}
              label="MQTT Topic"
              type="string"
              placeholder="some/mqtt/topic"
            />
          )}
          <div className={styles.marginLeft}>
            <InlineFormLabel width={12} className={hasManualTopic ? styles.marginLeftNone : styles.marginLeft}>
              Manual
            </InlineFormLabel>
          </div>
          <div className={cx(styles.marginLeft, styles.centered)}>
            <Switch width={12} onChange={this.onChangeHasManualTopic} value={hasManualTopic} />
          </div>
          {loading && (
            <div className={cx(styles.paddingLeft, styles.centered)}>
              <Spinner size={12} />
            </div>
          )}
        </div>
        <div className="gf-form">
          <InlineFormLabel width={12}>Messaging</InlineFormLabel>
          <Select
            width={24}
            options={messagingOptions}
            onChange={this.onChangeMessaging}
            value={messagingOptions.find(v => v.value === (messaging || Messaging.DEFAULT))}
          />
        </div>
        <div className="gf-form">
          <InlineFormLabel width={12}>Response Selector</InlineFormLabel>
          <div style={{ margin: 'auto 0' }}>
            <Switch width={12} onChange={this.onChangeHasResponseSelector} value={hasResponseSelector} />
          </div>
        </div>
        {hasResponseSelector && (
          <div className="gf-form">
            <FormField
              inputWidth={24}
              labelWidth={12}
              value={member}
              onChange={this.onMemberChange}
              label="JSON member"
              type="string"
              placeholder="some.member"
            />
          </div>
        )}
        <div className="gf-form">
          <FormField
            inputWidth={24}
            labelWidth={12}
            value={legend}
            onChange={this.onLegendChange}
            label="Legend"
            type="string"
            placeholder="Legend"
          />
        </div>
      </div>
    );
  }

  getStyles = stylesFactory(() => {
    return {
      marginLeftNone: css`
        margin-left: 0px;
      `,
      marginLeft: css`
        margin-left: 4px;
      `,
      paddingLeft: css`
        padding-left: 8px;
      `,
      centered: css`
        margin: auto 0;
        display: 'inline';
      `,
    };
  });
}
