import defaults from 'lodash/defaults';
import * as mqtt from 'mqtt';
import _ from 'lodash';

import {
  DataQueryRequest,
  DataQueryResponse,
  DataSourceApi,
  DataSourceInstanceSettings,
  FieldType,
  CircularDataFrame,
} from '@grafana/data';

import { MyQuery, MyDataSourceOptions, defaultQuery, defaultValues, Messaging } from './types';
import { Observable, merge } from 'rxjs';
import { MqttClient } from 'mqtt';

export class DataSource extends DataSourceApi<MyQuery, MyDataSourceOptions> {
  client: MqttClient;
  deviceId: string;

  constructor(instanceSettings: DataSourceInstanceSettings<MyDataSourceOptions>) {
    super(instanceSettings);
    const defaultValues$ = defaults(instanceSettings.jsonData, defaultValues);
    const protocol = defaultValues$.protocol;
    const host = defaultValues$.host;
    const port = defaultValues$.port;
    const authentication = defaultValues$.authentication;
    this.deviceId = defaultValues$.deviceId;

    const mqttOptions: any = { host, port, protocol };
    if (authentication) {
      mqttOptions.username = defaultValues$.username;
      mqttOptions.password = defaultValues$.password;
    }
    this.client = mqtt.connect(_.clone(mqttOptions));
  }

  query(options: DataQueryRequest<MyQuery>): Observable<DataQueryResponse> {
    const streams = options.targets.map(target => {
      const query = defaults(target, defaultQuery);
      query.legend = query.legend || 'value';

      return new Observable<DataQueryResponse>(subscriber => {
        const frame = new CircularDataFrame({
          append: 'tail',
          capacity: 1000,
        });

        frame.refId = query.refId;
        frame.addField({ name: 'time', type: FieldType.time });
        frame.addField({ name: query.legend, type: FieldType.other });
        let queryTopic = query.topic;
        if (queryTopic === '') {
          return;
        }
        if (query.messaging === Messaging.REQ_RES) {
          queryTopic += '/res';
        }
        if (!query.hasResponseSelector) {
          query.member = '';
        }
        if (this.client.disconnected) {
          this.client.reconnect();
        }
        this.client.unsubscribe(queryTopic);
        this.client.subscribe(queryTopic); // unsubscribe and subscribe: it gets `message` when we open editor
        let subscribeSomeValue = false;
        this.client.on('message', function(replyTopic: string, message: any) {
          if (queryTopic === replyTopic) {
            try {
              const parsedMessage = JSON.parse(message.toString());
              frame.add({
                time: Date.now(),
                [query.legend]: query.member ? _.get(parsedMessage, query.member) : parsedMessage,
              });
              subscriber.next({
                data: [frame],
                key: frame.refId,
              });
              subscribeSomeValue = true;
            } catch (e) {}
          }
        });
        // Even after 5 sec, if it doesn't receive any values from subscription
        // it sends a null value to let our panel renders
        setTimeout(() => {
          if (!subscribeSomeValue) {
            frame.add({ time: Date.now(), [query.legend]: null });
            subscriber.next({
              data: [frame],
              key: frame.refId,
            });
          }
        }, 5000);
      });
    });

    return merge(...streams);
  }

  async testDatasource() {
    // Implement a health check for your data source.
    return {
      status: 'success',
      message: 'Success',
    };
  }
}
